import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router'
import { AdCartComponent } from './ad-cart/ad-cart.component';
import { AdCheckoutComponent } from './ad-checkout/ad-checkout.component';
import { AdPlansComponent } from './ad-plans/ad-plans.component';
import { AdPlanDetailsComponent } from './ad-plan-details/ad-plan-details.component';

const advertisewithusRoutes: Routes=[
    {
        path: 'ad-cart',
        component: AdCartComponent
    },
    {
        path: 'ad-checkout',
        component: AdCheckoutComponent
    },
    {
        path: 'ad-plans',
        component: AdPlansComponent
    },
    {
        path: 'ad-plan-details',
        component: AdPlanDetailsComponent
    }
]

@NgModule({
    imports: [ RouterModule.forRoot(advertisewithusRoutes) ],
    exports: [ RouterModule ]
})
export class AdvertiseWithUsRoutingModule { }
