import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HomeComponent } from './home.component';

import { HomeDealsComponent } from './home-deals/home-deals.component';

import { HomeJobportalComponent } from './home-jobportal/home-jobportal.component';

const homeRoutes: Routes = [
    {
        path: 'home',
        component: HomeComponent,
    },
           
            {
                path: 'home-deals',
                component: HomeDealsComponent
            },
            
            {
                path: 'home-jobportal',
                component: HomeJobportalComponent
            },
           
    
]  

@NgModule({
  imports: [ RouterModule.forRoot(homeRoutes) ],
  exports: [ RouterModule ]
  
})
export class HomeRoutingModule { 
  constructor(){
    console.log();
  }
}
