import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ProductsComponent } from './products.component';
import { ProductsCategoryComponent } from './products-category/products-category.component';
import { ProductsSubcategoryComponent } from './products-subcategory/products-subcategory.component';
import { ProductsDetailComponent } from './products-detail/products-detail.component';
import { ProductsListComponent } from './products-list/products-list.component';
import { ProductExploreCatComponent } from './product-explore-cat/product-explore-cat.component';

const productsRoutes: Routes = [
    {
        path: 'products',
        component: ProductsComponent,
    },
           
            {
                path: 'products-category',
                component : ProductsCategoryComponent
            },
            {
                path: 'products-subcategory',
                component: ProductsSubcategoryComponent
            },
            {
                path: 'products-detail',
                component: ProductsDetailComponent
            },
            {
                path: 'products-list',
                component: ProductsListComponent
            },
            {
                path: 'product-explore-cat',
                component: ProductExploreCatComponent
            }
        
    
]

@NgModule({
   imports: [ RouterModule.forRoot(productsRoutes) ],
   exports: [ RouterModule ]
  
})
export class ProductsRoutingModule { }
