import { NgModule } from '@angular/core';
import { RouterModule ,Routes } from '@angular/router';
import { FavoritiesProductListComponent } from './favorities-product-list/favorities-product-list.component';
import { PostEnquiryComponent } from './post-enquiry/post-enquiry.component';
import { BigSaleComponent } from './big-sale/big-sale.component';

const favoritiesRoutes: Routes= [
  {
    path: 'favorities-product-list',
    component: FavoritiesProductListComponent
  },
  {
    path: 'post-enquiry',
    component: PostEnquiryComponent
  },
  {
    path: 'big-sale',
    component: BigSaleComponent
  }
]

@NgModule({
  imports: [ RouterModule.forRoot(favoritiesRoutes) ],
  exports: [ RouterModule ]
})
export class FavoritiesRoutingModule { }
