import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router'
import { SellerDirectoryComponent } from './seller-directory/seller-directory.component';


const sellerDirectoryRoutes: Routes=[
    {
        path: 'seller-directory',
        component: SellerDirectoryComponent
    }

]
@NgModule({
    imports: [ RouterModule.forRoot(sellerDirectoryRoutes) ],
    exports: [ RouterModule ]
})
export class SellerDirectoryRoutingModule { }
