import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router'
import { LatestEnquiryComponent } from './latest-enquiry/latest-enquiry.component';

const leadsRoutes: Routes= [
    {
        path: 'latest-enquiry',
        component: LatestEnquiryComponent
    }
]


@NgModule({
    imports: [ RouterModule.forRoot(leadsRoutes) ],
    exports: [ RouterModule ]
})
export class LeadsRoutingModule { }
